<template>
  <div id="collects">
    <div class="clearfix"></div>
    <div class="my-content">
    <h1>Collects</h1>

    <ul>
        <li v-for="user in users">
            <h4>{{user.name}}</h4>  <h5>{{user.email}}</h5>
        </li>
    </ul>
    </div>
  </div>
</template>

<script>

//局部注册组件
import Header from './Header-nav'

export default {
  name: 'Collects',
  data(){
    return {
        users:[

        ]
    }
  },
  //局部注册组件
  components:{
    "header-nav": Header,
  },
  //created页面加载前
  created(){
      this.$http.get("http://jsonplaceholder.typicode.com/users")
      .then((data)=>{
          console.log(data);
          this.users = data.body;
      })
  }
}
</script>

<style>
.my-content{
  background: rgb(248, 246, 246);
  height: 2000px;
  padding-top: 100px;

}
</style>



