<template>
  <div id="home">
    <div class=" container my-content">
      <content-collect></content-collect>
    </div>
  </div>
</template>

<script>

//局部注册组件
import Header from './Header-nav'
import Contentcollect from './Content-collect'

export default {
  name: 'Home',

  //局部注册组件
  components:{
    "header-nav": Header,
    "content-collect": Contentcollect,
  }
}
</script>

<style>

.my-content{
  background: rgb(248, 246, 246);
  height: 2000px;
  padding-top: 100px;

}
</style>



